<template>
  <div class="Up">
    <button class="ir-arriba" @click="scrollInto('element1')"><div class="arrow"></div></button>
  </div>
</template>
<script>
export default {
 name:"Date",
         data : () =>({
             scrl : ''
        }),
        methods:{
            scrollInto(elementId) {
                const section = document.querySelector(`#${elementId}`);
                section.scrollIntoView({
                behavior: 'smooth'
            });
        },
        scroll  : function ()
        { 
            window.onscroll = function (){
            // Obtenemos la posicion del scroll en pantall
            var scroll = document.documentElement.scrollTop || document.body.scrollTop;

            // Realizamos alguna accion cuando el scroll este entre la posicion 300 y 400
                if(scroll > 100 && scroll < 200)
                {
                  document.getElementsByClassName("ir-arriba")[0].style.display = "none";
                }
                if(scroll > 300 && scroll < 400){
                    // alert("ok");
                     document.getElementsByClassName("ir-arriba")[0].style.display = "block";
                }
            }
        }
    },
    mounted : function()
    {
        this.scroll();
    }
}

</script>

<style>
    .ir-arriba 
    {
        display:none;
        padding:12px;
        bottom: 50px;
        box-sizing: inherit;
        z-index: 99;
        border: none;
        outline: none;
        background-color: #ffd819;
        border-radius: 50%;
        height: 48px;
        width: 48px;
        margin-bottom: -10px;
        font-size:20px;
        color:#2c3e50;
        cursor:pointer;
        position: fixed;
        bottom:20px;
        right:2px;
    }	

    
div.arrow 
{
  width: 3vmin;
  height: 3vmin;
  box-sizing: border-box;
  position: absolute;
  left: 32%;
  top: 40%;
  transform: rotate(-45deg);
}
div.arrow::before 
{
  content: "";
  width: 100%;
  height: 100%;
  border-width: 0.8vmin 0.8vmin 0 0;
  border-style: solid;
  border-color: #2c3e50;
  transition: 0.2s ease;
  display: block;
  transform-origin: 100% 0;
}
div.arrow:after 
{
  content: "";
  float: left;
  position: relative;
  top: -100%;
  width: 100%;
  height: 100%;
  border-width: 0 0.8vmin 0 0;
  border-style: solid;
  border-color: #2c3e50;
  transform-origin: 100% 0;
  transition: 0.2s ease;
}

@media screen and (max-width:900px)
{
   .ir-arriba 
   {
        /* position: relative; */
        float: right;
        right: 20px;
   }
}
</style>