<template>
   <div class="content">
       <footer>
            <h3 class="pie">Alexis Lozada Desarrollador Web Full Stack</h3>
            <div class="copy">
              <small>&#169; {{ year }} {{code}}</small>
            </div>
       </footer>
   </div>
</template>

<script>
    export default {
         name:"Date",
         data : () =>({
           year: '',
           code : ''
        }),
         methods:{
           printYear: function () {
               return new Date().getFullYear();
            },
            printCode : function ()
            { 
               this.code = '</>';
            }
         },

         mounted: function(){
           this.year = this.printYear();
           this.printCode();
         }
    }
</script>

<style>
  footer
  {
    position: relative;
    background-color: #252932;
    width: 100%;
    height: 200px;
  }
  
  .pie
  {
      position: relative;
      top: 80px;
      color:#fff;
      font-size: 14px;
  }
  .copy
  {
    position: relative;
    top: 85px;
    font-size: 12px;
    color: #fff;

  }
</style>